import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import nama from './views/Login.vue'
import Konfirmasi from './views/Konfirmasi.vue'




Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login1',
      name: 'login1',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Login1.vue')
    },
    
     {
      path: '/konfirmasi',
      name: 'konfirmasi',
      component: Home
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      // component: () => import(/* webpackChunkName: "about" */ './views/Konfirmasi.vue')
    },
       {
      path: '/login',
      name: 'daftar_sekarang!',
      component: nama
    }

  ]
})
